.card-wrapper{
  max-width: 930px;
  margin: auto;
  padding: 10px;
}

ul{
  list-style:none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 142px);
  grid-gap: 15px;
  justify-content: center;
}

.card{
  width: 300px;
  height: 320px;
  background-color: rgba(112, 112, 253, 0.082);
  border-radius: 5px;
  overflow: hidden;
  text-align: center;
  
  img{
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
}

h1{
  text-align: center;
}


li{
  grid-column: span 2;
}


li:nth-child(3n - 1) {
  &:last-child{
    grid-column-start: 4;
  }
}

 li:nth-child(3n+1) {
 &:nth-last-child(2){
  grid-column-start: 2;
  }
}

 li:nth-child(3n-2) {
 &:nth-last-child(1){
  grid-column-start: 3;
  }
}


@media (max-width: 964px) {
  li {
    grid-column: span 2;
  }


  li:nth-child(3n - 1) {
    &:last-child{
      grid-column-start: 2;
    }
  }
  
   li:nth-child(3n+1) {
    &:nth-last-child(2){
      grid-column-start: span 2;
    }
  }
  
}

@media (max-width: 650px) {
  li {
    grid-column: 1;
  }
  ul{
    grid-template-columns: repeat(auto-fill, 300px);
  }

  li:nth-child(3n - 1) {
   &:last-child{
      grid-column-start: 1;    
    }
  }

  li:nth-child(3n+1) {
    &:nth-last-child(2){
      grid-column-start: 1;
    }
  }

  li:nth-child(3n-2) {
    &:nth-last-child(1){
      grid-column-start: 1;
    }
  }
  
  
}

